<template>
<section class="main-content">
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('RelatedProduct') }}
                        </h5>
                        <button v-on:click="ClosePopup()" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>
                    </div>
                    <tg-list :showCheckBox="true" :IsShowAction="false" :listType="['List']" ModuleName="AssetAssignment" SubModuleCode="AssetAssignment" IdentityColumn="ID" :HeaderText="$t('RelatedProduct')" :widgets="widgets" :ListData="GetRelatedProductData" :HeaderData="Header" :callbackfunction="FetchData" :LegendArray="LegendArray" :SearchFields="leftSearchFields" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NorecordfoundText')" :TotalRecords="TotalRecord" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData" v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                        <template v-slot:slotdata="{data}">
                            <template v-if="data.column.COLUMN_NAME == 'REQUESTED_QUANTITY'">
                                <input type="text" class="txtitems form-control txtAssetQuentity align-middle" :min="1" :value="1" />
                            </template>
                        </template>
                    </tg-list>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" @click="ClosePopup">{{ $t('Close')
                                    }}</button>
                        <button type="button" id="relatedprdct" class="btn btn-success" @click="SaveChanges">{{
                                        $t('SaveChanges') }}</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
</template>

<script>
import DataService from '../../service/DataService';
export default {
    props: {
        RelatedProductDaata: {
            type: Array,
            required: true
        },
        AssetListData: {
            type: Array,
            required: true
        },
        GetRelatedProduct: {
            type: Array
        },
        ItemIndex: {
            type: Number
        }
    },
    data() {
        return {
            widgets: [],
            leftSearchFields: '',
            listheaderbuttons: [],
            LegendArray: [],
            GetRelatedProductData: [],
            IsChecked: false,
            txtAssetQuentity: 1,
            RelatedProduct: [],
            Header: [{
                    "COLUMN_NAME": "NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("ProductName"),
                    "DISPLAY_ORDER": 1,

                },
                {
                    "COLUMN_NAME": "AVAILIABLE_ITEM",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("AvailableQuantity"),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "REQUESTED_QUANTITY",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("RequiredQuantity"),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
            ],
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalPage: 0,
            TotalRecord: 0,
            noRecord: true,
            PageNumber: 1,
            PageSize: 10,
        }
    },
    created: function () {
        var vm = this;
        if (vm.GetRelatedProduct != null && vm.GetRelatedProduct.length > 0) {
            vm.GetRelatedProductData = vm.GetRelatedProduct; 
            setTimeout(function () {
            vm.CheckBoxBootstrap();
            }, 100);
            setTimeout(function () {
                vm.ResponsiveDataTable('tablelistingdata')
            }, 400);
            setTimeout(function () {
                $('#chkAll_0').trigger('click');
            }, 600);           
        } else {
            vm.FetchData();
        }
        

    },
    methods: {

        FetchData: function () {
            var vm=this;
            var url = `catId=${this.RelatedProductDaata[0].AssetCatalogueId}&type=ProductName`;
            DataService.GetRelatedProduct(url).then(response => {
                if (response.data.result.length > 0) {
                    this.GetRelatedProductData = JSON.parse(JSON.parse(response.data.result).RESULT);
                    //this.TotalRecords = response.data.data[0].TOTAL_PAGES;
                    //this.TotalPages = Math.ceil(this.TotalRecords / this.PageSizes);
                    // this.CurrentPages = this.PageNumbers;
                    this.noRecord = false;
                    this.isLoading = false;
                } else {
                    this.noRecord = true;
                    this.GetRelatedProductData = [];
                    this.TotalRecords = 0
                    this.isLoading = false;
                }
                setTimeout(function () {
                    vm.CheckBoxBootstrap();
                }, 300);
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata');
                }, 400);
                setTimeout(function () {
                    $('#chkAll_0').trigger('click');
                }, 600);

            })
        },
        SaveChanges: function () {
            var vm = this;
            if (vm.GetRelatedProduct != null && vm.GetRelatedProduct.length > 0) {
                vm.$parent.UpdateProductList(vm.txtAssetQuentity, vm.ItemIndex);
            } else {
                var locationId = vm.RelatedProductDaata[0].LocationId;
                var selectedProductArray = [];
                vm.AssetListData.forEach((item) => {
                    selectedProductArray.push({
                        catalogueId: item.AssetCatalogueId,
                        uniqueId: item.ParentUniqueId
                    })
                });
                vm.GetRelatedProductData.forEach((item, index) => {
                    if (vm.IsChecked) {
                        var relationType = item.ID == '0' ? 'Product' : 'ProductName';
                        var id = relationType == 'ProductName' ? item.ID : vm.RelatedProductDaata[0].ParentUniqueId;
                        var isMandatory = item.Mandatory;
                        var relatedCatalogueId = vm.RelatedProductDaata[0].AssetCatalogueId;
                        var relatedUniqueId = vm.RelatedProductDaata[0].ParentUniqueId;
                        var AssetQuentity = vm.txtAssetQuentity;
                        if (selectedProductArray.some(x => x.catalogueId == vm.RelatedProductDaata[0].AssetCatalogueId && vm.RelatedProductDaata[0].ParentUniqueId)) {
                            var catalogueIds = relationType == 'ProductName' ? id : null;
                            var uniqueIds = relationType == 'Product' ? id : null;
                            var assignedFrom = this.$options.filters.formatDate(vm.$parent.$parent.$parent.$parent.DateofAssignment);
                            var assignedTill = this.$options.filters.formatDate(vm.$parent.$parent.$parent.$parent.DateofAssignmentTill);
                            var IssueTo = vm.$parent.$parent.$parent.$parent.Users == undefined ? this.$options.filters.formatDate(vm.$parent.$parent.$parent.$parent.Users) : 0;
                            var startTime = this.$options.filters.formatTime(assignedFrom);
                            var endTime = this.$options.filters.formatTime(assignedTill);
                            var url = `id=&isView=1&barcode=&assetType=&assetUniqueId=&selfCheckout=&reqFrom=assetassignment&isFor=&isuniqueSearch=&issueTo=${IssueTo}&assignedFrom=${assignedFrom}&assignedTill=${assignedTill}&startTime=${startTime}&endTime=${endTime}&locationId=${locationId}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=${uniqueIds}&catalogueIds=${catalogueIds}`;
                            DataService.GetCompanyAssestCatalogsDetalis(url).then((response) => {
                                vm.RelatedProduct = response.data;
                                vm.$emit('RelatedProduct', vm.RelatedProduct);
                                vm.ClosePopup();
                            });
                        }
                    } else {
                        vm.$parent.Isrelatedproduct = false;
                    }
                });
            }
        },

        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNum = value.PageNumber;
            this.FetchData();
        },

        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            if (this.activeTab == 1 || this.activeTab == 4) {
                this.FetchData();
            } else if (this.activeTab == 2) {
                this.GetAssetReplacement();
            } else {
                this.GetAssetReturnRequestList();
            }
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },

        ClosePopup: function () {
            this.$parent.CloseRelatedPopup();
        },
        handleClick() {
            if (this.$refs.theCheckbox[0].checked) {
                this.IsChecked = true;
            } else {
                this.IsChecked = false;
            }
        },
        commonHeaderButtonClick: function () {},
        renderActions() {},
        renderRowActionMethod() {},
    }
}
</script>

<style>
.custom-radio .custom-control-input:checked~.custom-control-label::after,
.custom-checkbox .custom-control-input:checked~.custom-control-label::after {
    display: block;
}
</style>
